import { Alert } from "@mui/material";

const StatusAlert = ({ pm25 }) => {
  if (pm25 > 100) {
    return <Alert severity="error">‚ö†Ô∏è High Pollution Level! PM2.5: {pm25} ¬µg/m¬≥</Alert>;
  } else if (pm25 > 50) {
    return <Alert severity="warning">‚ö†Ô∏è Moderate Pollution! PM2.5: {pm25} ¬µg/m¬≥</Alert>;
  }
  return <Alert severity="success">‚úÖ Air Quality is Good!</Alert>;
};


export default StatusAlert;